@import url(https://fonts.googleapis.com/css2?family=Emilys+Candy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Emilys+Candy&display=swap);
/*
==============================
HOME
==============================
*/


.homeBody {
    background-color: #252627;
    margin-bottom: 0rem;
    height: 130vh;
}

.homeContainer {
    width: 100vw;
    height: 30.5vh;
    margin-bottom: 10%;
}

.aboutPic {
    width: 250px;
    height: 250px;
    margin-top: 2%;
    display: block;
    margin-left: auto;
    margin-right: auto;

}

.text h1 {
    color: #fff;
    font-size: 3.5rem;
    cursor: context-menu;
    justify-content: space-between;
    letter-spacing: 20px;
}

.text span {
    display: inline-block;
    font-size: 4rem;
    color: rgb(195, 136, 230);
    font-family: 'Emilys Candy',
        cursive;
    letter-spacing: 5px;
}

span:hover {
    color: #b0daee;
    transition: all .5s;
}

span.m-left {
    margin-left: -3px;
}

.text p {
    letter-spacing: 3px;
    word-spacing: 10px;
    font-size: 1.8rem;
}

.text a:hover {
    color: #fff;
}

.text a::before {
    content: '';
    background-color: #9f82e2;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 0%;
    z-index: -1;
    transition: .5s;
}

.text a:hover::before {
    height: 100%;
}

/* make the header letters shake when hoover */
.text span:hover,
.m-left:hover,
.name:hover {
    animation: shake 0.5s;
    animation-iteration-count: infinite;
}

@keyframes shake {
    0% {
        transform: translate(1px, 1px) rotate(0deg);
    }

    10% {
        transform: translate(-1px, -2px) rotate(-1deg);
    }

    20% {
        transform: translate(-3px, 0px) rotate(1deg);
    }

    30% {
        transform: translate(3px, 2px) rotate(0deg);
    }

    40% {
        transform: translate(1px, -1px) rotate(1deg);
    }

    50% {
        transform: translate(-1px, 2px) rotate(-1deg);
    }

    60% {
        transform: translate(-3px, 1px) rotate(0deg);
    }

    70% {
        transform: translate(3px, 1px) rotate(-1deg);
    }

    80% {
        transform: translate(-1px, -1px) rotate(1deg);
    }

    90% {
        transform: translate(1px, 2px) rotate(0deg);
    }

    100% {
        transform: translate(1px, -2px) rotate(-1deg);
    }
}

.containerP {
    padding: 0 100px 0 100px;
}

.banner-text {
    position: absolute;
    top: 75%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.banner-text p {
    font-family: 'Emilys Candy', cursive;
    color: #fff;
}

.text-area h1 {
    font-size: 3.5rem;
    cursor: pointer;
    font-family: 'Emilys Candy',
        cursive;
    justify-content: space-between;
    letter-spacing: 12%;
}


.text-area span {
    color: #fff;
    opacity: 0;
    transform: translate(0, -100px) rotate(360deg) scale(0);
    animation: animate 5s forwards;
}

.text-area span {
    display: inline-block;
    color: #a69cbe;

}

.text-area span:nth-of-type(2) {
    animation-delay: 1s;
    color: #cdaac0;
}

.text-area span:nth-of-type(3) {
    animation-delay: 2s;
    color: #e8b7ca;
}

.text-area span:nth-of-type(4) {
    animation-delay: 3s;

}

.text-area span:nth-of-type(5) {
    animation-delay: 4s;
    color: #fed6df;
}

.text-area span:nth-of-type(6) {
    animation-delay: 5s;
    color: #b0daee;
}

.text-area span:nth-of-type(7) {
    animation-delay: 6s;
    color: #a69cbe;
}

.text-area span:nth-of-type(8) {
    animation-delay: 7s;

}

.text-area span:nth-of-type(9) {
    animation-delay: 7s;
    color: #cdaac0;

}

.text-area span:nth-of-type(10) {
    animation-delay: 7s;
    color: #e8b7ca;

}

@keyframes animate {
    30% {
        transform: translate(0, -50px) rotate(180deg) scale(1);
    }

    60% {
        transform: translate(0, 20px) rotate(0deg) scale(0.8s);
    }

    100% {
        transform: translate(0) rotate(0deg) scale(1);
        opacity: 1;
    }
}

main {
    padding-top: 30px;
    padding-bottom: 0%;
}
.wrapperAbout {
    background-color: #252627;
}

#about {
    width: 100vw;
    height: 120vh;
    margin-top: 0%;
}

.about {
    height: 80vh;
    margin-top: 80px;
}

.about h1,
.contact h1,
.portfolio h1 {
    font-size: 6rem;
    z-index: 0;
    color: #a69cbe;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-family: 'Emilys Candy',
        cursive;
}

.about-me-info {
    max-width: 1300px;
    background-color: #a69cbe;
    box-shadow: 0 0 1.4rem rgba(0, 0, 0, .25);
    padding: 50px;
    position: relative;
    top: 50px;
    left: 50%;
    right: 50%;
    transform: translateX(-50%);
    z-index: 1;

}

.about-me-info p {
    font-size: 1.3rem;
    color: black;
}

.about-me-info p:nth-child(2),
.about-me-info p:last-child {
    margin-top: 30px;
}
ul {
    list-style-type: none;
    color: whitesmoke;
    font-family: 'Emilys Candy',
        cursive;
}

a {
    text-decoration: none;

}

h1,
h2,
h3,
h4 {
    letter-spacing: 0.1rem;
    text-transform: capitalize;
    line-height: 1.25;
    margin-bottom: 0.75rem;
}

h1 {
    font-size: 3rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.25rem;
}

h4 {
    font-size: 0.875rem;
}

p {
    margin-bottom: 1.25rem;
    color: hsl(210, 22%, 49%);
}

@media screen and (min-width: 800px) {
    h1 {
        font-size: 4rem;
    }

    h2 {
        font-size: 2.5rem;
    }

    h3 {
        font-size: 1.75rem;
    }

    h4 {
        font-size: 1rem;
    }

    body {
        font-size: 1rem;
    }

    h1,
    h2,
    h3,
    h4 {
        line-height: 1;
    }
}

/*  global classes */

/* section */
.section {
    width: 90vw;
    margin: 0 auto;
    max-width: 1170px;
}

@media screen and (min-width: 992px) {
    .section {
        width: 95vw;
    }
}

/*
=============== 
Navbar
===============
*/


nav {
    background: #19191b;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.nav-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
}

.nav-header h4 {
    color: whitesmoke;
    font-family: 'Emilys Candy',
        cursive;
}

.nav-toggle {
    font-size: 1.5rem;
    color: hsl(205, 78%, 60%);
    background: transparent;
    border-color: transparent;
    transition: all 0.3s linear;
    cursor: pointer;
}

.nav-toggle:hover {
    color: hsl(205, 86%, 17%);
    transform: rotate(90deg);
}

.logo {
    height: 40px;
}

.links a {
    /* color: hsl(209, 34%, 30%); */
    font-size: 1rem;
    text-transform: capitalize;
    letter-spacing: 0.1rem;
    display: block;
    padding: 0.5rem 1rem;
    transition: all 0.3s linear;
    color: #a69cbe;
    font-family: 'Emilys Candy',
        cursive;
}

.links a:hover {
    background: hsl(205, 86%, 81%);
    color: hsl(205, 78%, 60%);
    padding-left: 1.5rem;
}

.links-container {
    height: 0;
    overflow: hidden;
    transition: all 0.3s linear;
}

.show-container {
    height: 10rem;
}

@media screen and (min-width: 800px) {
    .nav-center {
        max-width: 1170px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 1rem;
    }

    .nav-header {
        padding: 0;
    }

    .nav-toggle {
        display: none;
    }

    .links-container {
        height: auto !important;
    }

    .links {
        display: flex;
    }

    .links a {
        padding: 0;
        margin: 0 0.5rem;
    }

    .links a:hover {
        padding: 0;
        background: transparent;
    }

}
.footer {
    background-color: #181818;
    color: rgb(194, 189, 181);
    font-family: 'Emilys Candy',
        cursive;
    margin-top: 0;
    padding: 1rem;
    position: flex;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;

}
