.body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Emilys Candy',
        cursive;
    font-size: large;
    color: #000;
    background-color: #252627;

}

.containerLogin {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 1200px;
    flex-wrap: wrap;
    z-index: 1;
}

label {
    margin: 2%;

}

input {
    width: 100%;
}

span,
button {
    padding: 5px;
}

button:hover,
span:hover {
    color: blue;
}

.contentCard img {
    width: 150px;
    height: 150px;

}

.contentCard button {
    margin-top: 10%;
}

.body h1 {
    font-size: 6rem;
    z-index: 0;
    color: #a69cbe;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-family: 'Emilys Candy',
        cursive;
}

.containerLogin .cardS {
    position: relative;
    width: 280px;
    height: 400px;
    margin: 30px;
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
}

.containerLogin .cardS .contentCard {
    padding: 20px;
    text-align: center;

}

.containerLogin .cardS .contentCard h2 {
    position: absolute;
    top: -80px;
    right: 30px;
    font-size: 8em;
    color: rgba(255, 255, 255, 0.05);
    pointer-events: none;
}


.containerLogin .cardS .contentCard p {
    font-size: 1em;
    color: #000;
    font-weight: 300;
}
/*
